.sr-only, .screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.gallery {
  margin-bottom: 1.5em;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
}
.gallery-columns-2 .gallery-item {
  max-width: 50%;
}
.gallery-columns-3 .gallery-item {
  max-width: 33.33%;
}
.gallery-columns-4 .gallery-item {
  max-width: 25%;
}
.gallery-columns-5 .gallery-item {
  max-width: 20%;
}
.gallery-columns-6 .gallery-item {
  max-width: 16.66%;
}
.gallery-columns-7 .gallery-item {
  max-width: 14.28%;
}
.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}
.gallery-columns-9 .gallery-item {
  max-width: 11.11%;
}

.gallery-caption {
  display: block;
}

.main-menu {
  font-size: 2rem;
}

.nav-link {
  color: #0a0a0a;
  font-size: 1.36rem;
  font-family: "Archivo", sans-serif;
}

.nav-link:hover {
  color: #f5f5f8;
}

.header-front {
  text-align: center;
  background-image: url(http://localhost/adapt-local/wp-content/uploads/2021/10/P1220286.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 10rem 0rem 20rem 0rem;
}
.header-front h1 {
  color: white;
  font-size: 2rem;
  width: 75%;
  margin: 0 auto;
  margin-bottom: 2rem;
}

main {
  width: 100%;
}

#page-wrapper {
  padding: 0rem;
}

.nav-bar .container {
  padding: 0rem;
}

.menu-item-29 {
  background-color: #8ee633;
  text-align: center;
  border-radius: 1rem 0.2rem;
}

.menu-item-29:hover {
  background-color: #2a5417;
  color: #f5f5f8;
  border-radius: 1rem 0.2rem;
}

.site-title {
  font-size: 2rem;
  font-family: "Archivo", sans-serif;
  margin: 0rem 0rem 0rem auto;
  color: #0a0a0a;
  max-width: 10.9rem;
  line-height: 1.25;
}

@media (max-width: 400px) {
  .site-title,
.site-info p {
    font-size: 1.5rem;
    width: 8rem;
  }
}
#navbarNavDropdown {
  overflow: hidden;
  max-width: 43rem;
}

.navbar-brand {
  margin-right: 0rem;
}

.hollow-btn {
  padding: 1rem;
  background-color: #f5f5f8;
  color: #2a5417;
  border: solid 2px #2a5417;
  box-shadow: none;
  display: block;
  max-width: 15rem;
  text-align: center;
}

.hollow-btn:hover {
  cursor: pointer;
  background-color: #2a5417;
  text-decoration: none;
  color: #f5f5f8;
}

.btn-primary {
  padding: 1rem;
  background-color: #b2a6cf;
  color: white;
  border: solid 2px #b2a6cf;
  box-shadow: none;
  display: block;
  max-width: 15rem;
  text-align: center;
}

.btn-primary:hover {
  cursor: pointer;
  background-color: #f5f5f8;
  text-decoration: none;
  color: #b2a6cf;
}

.post-60,
.post-125 {
  width: 100%;
}

.site-main section {
  padding-top: 5rem;
}

.community-child-home {
  text-align: center;
  display: block;
  flex-grow: 1;
  margin-bottom: 5rem;
}
.community-child-home img {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  margin-bottom: 2rem;
}
.community-child-home a {
  display: block;
  margin: 0 auto;
}
.community-child-home h3 {
  margin-bottom: 2rem;
}

.banner-front-page {
  padding: 10rem 0rem 25rem 0rem;
