}
.banner-front-page p {
  max-width: 15rem;
  margin: 0 auto;
}
.banner-front-page h1 {
  font-family: "Archivo", sans-serif;
  color: #f5f5f8;
  max-width: 60rem;
  margin: 0 auto;
  padding: 0rem 1rem 0rem 1rem;
  margin-bottom: 3rem;
  font-weight: 400;
